import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import {AuthService} from './services'
import {
  LoginComponent,
  DashboardMainComponent,
  DashboardPanelComponent,
  CreateAccountComponent,
  ActivateAccountComponent,
  LiveChannelComponent,
  PlayListComponent,
  VideoOnDemandComponent
} from './components'

const routes: Routes = [
  { path: 'login', component: LoginComponent },
  {path: 'register', component: CreateAccountComponent},
  { path: 'activate/:id', component: ActivateAccountComponent },
  {
    path: 'dashboard',
    canActivate: [AuthService],
    component: DashboardMainComponent, // router-outlet
    children: [{
      path: '',
      component: DashboardPanelComponent
    }, {
      path: 'live',
      component: LiveChannelComponent
    }, {
      path: 'vod',
      component: VideoOnDemandComponent
    }, {
      path: 'playlist',
      component: PlayListComponent
    },  {
      path: 'account',
      loadChildren: './components/account/account.module#AccountModule',
      data: {preload: true}
    },  {
      path: 'analytics',
      loadChildren: './components/analytics/analytics.module#AnalyticsModule',
      data: {preload: true}
    },  {
      path: 'preferences',
      loadChildren: './components/preferences/preferences.module#PreferencesModule',
      data: {preload: true}
    }]
  },
  { path: '**', redirectTo: 'login' }
]

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



