import { Component, Input, Output, EventEmitter } from '@angular/core'
import {AuthService} from '../../services'

@Component({
  selector: 'top-bar',
  templateUrl: './topbar.html',
  styleUrls: ['./topbar.scss']
})
export class TopBarComponent {
  @Input() user$
  @Output() signout = new EventEmitter()

  logout () {
    this.signout.next()
  }

}



