import { Injectable } from '@angular/core'
import { ApiService } from './api.service'
import 'rxjs/add/operator/map'

@Injectable()
export class VideoOnDemandService {

  constructor(private api: ApiService) {}

  getVideo() {
    return this.api.get('/player/code/123')
      .map((res: any) => res)
  }

}



