import {Component, ViewChild, ElementRef} from '@angular/core'
import {VideoOnDemandService} from '../../services'

@Component({
  templateUrl: './vod.html',
  styleUrls: ['./vod.scss']
})
export class VideoOnDemandComponent {
  @ViewChild('dataContainer') dataContainer: ElementRef;

  html

  constructor(private videoService: VideoOnDemandService) {}

  ngAfterViewInit() {
    this.videoService.getVideo()
      .subscribe(({code}) => this.dataContainer.nativeElement.innerHTML = code)
  }

}



