import { Component, ViewChild, Renderer } from '@angular/core';

@Component({
  selector: 'side-bar',
  templateUrl: './sidebar.html',
  styleUrls: ['./sidebar.scss']
})
export class SideBarComponent {
  @ViewChild('analyticsRef') analyticsRef
  @ViewChild('accountRef') accountRef
  @ViewChild('preferencesRef') preferencesRef

  private opened = [false, false, false]

  style: any = {
    'display': 'none'
  }

  constructor(private renderer:Renderer) {}

  openMenu (ref) {
    let element
    this.opened = this.opened.map((open, i) => {
      if (i === ref - 1) {
          return !open
      }
      return open
    })

    const display = (this.opened[ref - 1]) ? 'block' : 'none'

    switch (ref) {
      case 1:
        element = this.analyticsRef
      break
      case 2:
        element = this.accountRef
      break
      case 3:
        element = this.preferencesRef
      break
    }

    this.renderer.setElementStyle(element.nativeElement, 'display', display)
  }

}



