import {NgModule} from '@angular/core'
import { RouterModule } from '@angular/router'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'

import {
  SideBarComponent,
  TopBarComponent,
  CreditCardModalComponent,
  PaypalModalComponent,
  BankAccountModalComponent,
  ModifyStorageModal,
  PaypalFormComponent,
  CheckFormComponent,
  InternationalTransferFormComponent,
  USTransferFormComponent
} from './'


@NgModule({
  imports: [
    RouterModule,
    CommonModule,
    FormsModule
  ],
  declarations: [
    SideBarComponent,
    TopBarComponent,
    CreditCardModalComponent,
    PaypalModalComponent,
    BankAccountModalComponent,
    ModifyStorageModal,
    PaypalFormComponent,
    CheckFormComponent,
    InternationalTransferFormComponent,
    USTransferFormComponent
  ],
  exports: [
    SideBarComponent,
    TopBarComponent,
    CreditCardModalComponent,
    PaypalModalComponent,
    BankAccountModalComponent,
    ModifyStorageModal,
    PaypalFormComponent,
    CheckFormComponent,
    InternationalTransferFormComponent,
    USTransferFormComponent
  ]
})
export class UIModule {}



