import { Injectable } from '@angular/core'
import { ApiService } from './api.service'
import {Observable} from 'rxjs/Observable'
import { Subject } from 'rxjs/Subject'
import {CanActivate, Router} from '@angular/router'
import {BehaviorSubject} from 'rxjs/BehaviorSubject'
import {JwtHelper} from 'angular2-jwt'
import 'rxjs/add/operator/do'
import 'rxjs/add/operator/map'

declare const window: any

@Injectable()
export class AuthService implements CanActivate {
  private jwtHelper: JwtHelper = new JwtHelper()
  private JWT_KEY: string = 'easycast_token'
  private JWT: string = ''
  private userDefault: any = {
    email: '',
    role: 0,
    account_id: 0
  }
  USER: BehaviorSubject<any> = new BehaviorSubject<any>(this.userDefault)

  constructor(
     private api: ApiService,
     private router: Router
   ) {
    const token = window.localStorage.getItem(this.JWT_KEY)
    if (token) {
      this.setValues({token})
    }
  }

  setValues(res: any) {

    console.log('entered')
    if (!res.token) {
        return res
    }

    console.log(this.jwtHelper.decodeToken(res.token))

    this.USER.next(this.jwtHelper.decodeToken(res.token))
    window.localStorage.setItem(this.JWT_KEY, res.token)
    this.api.setHeaders({Authorization: `Bearer ${res.token}`})
    return res
  }

  isAuthorized(): boolean {
    return Boolean(window.localStorage.getItem(this.JWT_KEY))
  }

  canActivate(): boolean {
    const canActivate = this.isAuthorized()
    this.onCanActivate(canActivate)
    return canActivate
  }

  onCanActivate(canActivate: boolean) {
    if (!canActivate) {
      this.router.navigate(['', 'login'])
    }
  }

  authenticate(path, creds): any {
    return this.api.post(`/auth/${path}`, creds)
    .do((res: any) => {
      this.setValues(res)
    })
    .map((res: any) => res )
  }

  validateAccount (id): any {
    return this.api.get(`/auth/active/${id}`)
      .do((res: any) => this.setValues(res))
      .map((res: any) => res)
  }

  signout() {
    window.localStorage.removeItem(this.JWT_KEY)
    this.USER.next(this.userDefault)
    this.router.navigate(['', 'login'])
  }
}



