import { Injectable } from '@angular/core'
import { ApiService } from './api.service'
import {Observable} from 'rxjs/Observable'
import { Subject } from 'rxjs/Subject'
import 'rxjs/add/operator/map'

declare const window: any

@Injectable()
export class AccountService {

  private userAPI: string = '/restricted/account/user'
  private companyAPI: string = '/restricted/account/company'

  constructor(private api: ApiService) {}

  createUser (user: any) {
    return this.api.post(`${this.userAPI}/register`, user)
      .map((res: any) => res)
  }

  getUser (user_id) {
    return this.api.get(`${this.userAPI}/profile/${user_id}`)
      .map((res: any) => res)
  }

  getUsers (id) {
    return this.api.get(`${this.userAPI}/${id}`)
      .map(({users}) => users)
  }

  getCompany (account_id) {
    return this.api.get(`${this.companyAPI}/${account_id}`)
      .map((res: any) => res)
  }

  updateValues (user) {
    return this.api.post(`${this.userAPI}/update`, user)
      .map(({users}) => users)
  }

  updateProfile (user) {
    return this.api.post(`${this.userAPI}/profile`, user)
      .map((res: any) => res)
  }

  updateCompany (account_id, company) {
    return this.api.post(`${this.companyAPI}/${account_id}`, company)
      .map((res: any) => res)
  }

}



