import {NgModule, ModuleWithProviders} from '@angular/core'
import {
  ApiService,
  AuthService,
  AccountService,
  UtilsService,
  VideoOnDemandService
} from './'

@NgModule({})
export class ServicesModule {
  static forRoot (): ModuleWithProviders {
    return {
      ngModule: ServicesModule,
      providers: [
        ApiService,
        AuthService,
        AccountService,
        UtilsService,
        VideoOnDemandService
      ]
    }
  }
}



